<template>
	<!-- 头部导航栏 -->
	<nav-bar title='地址簿'></nav-bar>
	<view v-if="list.data.length > 0" class="list-container">
		<scroll-view  scroll-y lower-threshold="30" @scrolltolower="LoadMoreCustomers">
			<!-- 列表内容 -->
			<view v-for='(item,index) in list.data' @click="handleToAddressInfo(item.id)" :key='index'
				class="address-list" :class="isManage?'isChecked':''">
				<view class="checkbox" v-if="isManage">
					<view class="checkRadio">
						<radio color="#F23C24" :value="String(index)" :checked="item.selected"
							@click="checkbox(index,item)" />
					</view>
				</view>
				<view class="address-item" :class="isManage?'isChecked':''">
					<view class="address-name">{{item.name}}</view>
					<view class="address-phone">{{item.phoneNumber}}</view>
					<view class="address-address">
						{{item.province? item.province.name+item.city.name+ item.county.name+item.address:''}}
					</view>
					<view class="line"></view>
					<view class="address-item-footer">
						<view class="edit-btn" @click="handleEditAddress(item)">
							<icon></icon>
							<text>编辑</text>
						</view>
						<view class="delete-btn" @click="handledDelete(item.id)">
							<icon></icon>
							<text>删除</text>

						</view>
					</view>
				</view>
			</view>
			<!-- 加载底部 -->
			<uni-load-more :status="status" />
		</scroll-view>
		<!-- 全选操作栏 -->
		<view class="all-select" v-if="isManage">
			<view class="checkbox" @click="selectAll">
				<view class="checkRadio">
					<radio :checked="isAllSelect" color="#F23C24" />
				</view>
			</view>
			<view>全选</view>
			<!-- <uni-radio>123</uni-radio> -->
		</view>
		<!-- 底部操作栏 -->
		<view class="address-footer">
			<view class="manage-btn" @click="handleEdit">{{isManage?'完成':'管理'}}</view>
			<view class="addAddress-btn" @click="handleDeleteOrAdd">{{isManage?'删除':'新增地址'}}</view>
		</view>
	</view>
	<!-- 空页面 -->
	<view v-else>
		<EmptyPage :emptyData="emptyData"></EmptyPage>
	</view>

	<!-- 删除确认对话框 -->
	<uni-popup ref="popup" type="dialog" class='address-popup'>
		<uni-popup-dialog mode="base" :content="isManage?'确定是否删除所选地址？':'确定是否删除此条地址？'" :animation='false'
			:before-close="true" @close="close" @confirm="confirm">

		</uni-popup-dialog>
	</uni-popup>

</template>

<script setup>
	import {
		ref,
		reactive,
		onMounted,
		watch,
		nextTick
	} from 'vue';
	import {
		getAddressList,
		deleteAddress
	} from '@/pages/api/address.js'
	import {
		onLoad,
		onShow
	} from '@dcloudio/uni-app';
	let popup = ref(null)
	let isAllSelect = ref(false)
	let isManage = ref(false)
	let emptyData = ref('暂无数据');
	let deleteIds = reactive({
		data: [] //删除的地址id集合
	})
	let list = reactive({
		data: [] //列表数据
	})
	let pageInfo = reactive({
		page: 1,
		pageSize: 10
	})
	let status = ref('more') //加载状态
	let height = ref('670px')
	let type = ref('')
	onLoad((options) => {
		console.log(options, 'options.type')
		type.value = options.type
	})
	onMounted(() => {
		getList()
	})

	//跳转回寄件收件选择地址
	const handleToAddressInfo = (id) => {
		if(type.value === 'address'){
			return
		}
		uni.redirectTo({
			url: '/pages/express-delivery/index?type=' + type.value +'&editOrAdd=edit&id=' + id +
				'&isFromAddress=true'
		});
	}
	//批量删除或者新增地址
	const handleDeleteOrAdd = () => {
		// console.log()
		if (isManage.value) {
			popup.value.open()
		} else {
			uni.redirectTo({
				url: '/subPages/address-info/index?isFromAddress=true&editOrAdd=add'
			});
		}
	}
	//编辑地址
	const handleEditAddress = (item) => {
		uni.redirectTo({
			url: '/subPages/address-info/index?id=' + item.id + '&isFromAddress=true' + '&editOrAdd=edit'
		});
	}
	//获取列表数据
	const getList = (flag) => {
		status.value = 'loading'
		getAddressList({
			page: pageInfo.page,
			pageSize: pageInfo.pageSize
		}).then((res) => {
			status.value = res.data.items.length < 10 ? 'no-more' : 'more'
			//用于区分是一次进页面记载数据还是后续上拉加载
			if (flag === 'topPull') {
				list.data = list.data.concat(res.data.items)
			} else {
				list.data = res.data.items
			}
			// console.log(!res.data.items[0].province, list.data, 'res')
		})
	}
	//上拉加载
	const LoadMoreCustomers = () => {
		pageInfo.page = pageInfo.page + 1
		if (status.value === 'no-more') {
			return
		}
		getList('topPull')
		console.log('上拉加载')
	}
	//全选
	const handledAllSelect = () => {
		console.log(222)
		isAllSelect.value = !isAllSelect.value
	}
	//管理地址簿
	const handleEdit = () => {
		console.log(111)
		isManage.value = !isManage.value
	}

	//删除
	const handledDelete = (id) => {
		popup.value.open()
		deleteIds.data.push(id)
	}

	//关闭删除确认提示框
	const close = () => {
		popup.value.close()
		deleteIds.data = []
	}

	//确认删除
	const confirm = () => {
		popup.value.close()
		// list.data = []
		console.log(deleteIds.data, 'deleteIds.data')
		deleteAddress(deleteIds.data).then((res) => {
			console.log(res, 'res')
			pageInfo.page = 1
			pageInfo.pageSize = 10
			getList()
			uni.showToast({
				title: '删除成功',
				icon: 'success',
				duration: 1000
			})
		})
	}

	//选中
	const checkbox = (index, item) => {
		list.data[index].selected = !list.data[index].selected
		deleteIds.data = list.data.filter(item => item.selected).map(item => item.id)
		console.log(list.data, 'list.data')
	}
	//全选
	const selectAll = () => {
		console.log(isAllSelect.value, 'isAllSelect.value')
		isAllSelect.value = !isAllSelect.value
		list.data = list.data.map((item) => {
			return Object.assign({}, item, {
				selected: isAllSelect.value
			})
		})
		deleteIds.data = list.data.filter(item => item.selected).map(item => item.id)
	}
</script>

<style src="./index.scss" lang="scss" scoped></style>
